:root {
  --primary-color: #FF7829;
  /* Orange */
  --secondary-color: #32c737;
  /* Green */
  --button-color: #000000;
  /* Button */
  --white: #ffffff;
  --black: #000000;
}

body.high-contrast {
  --primary-color: #000000;
  /* Black */
  --secondary-color: #000000;
  /* Black */
  --button-color: #FF7829;
  /* Button */
}

@font-face {
  font-family: 'Archivo';
  src: url('./assets/fonts/Archivo-Regular.ttf');
  font-weight: 400;
}

@font-face {
  font-family: 'Archivo';
  src: url('./assets/fonts/Archivo-Light.ttf');
  font-weight: 300;
}

@font-face {
  font-family: 'Archivo';
  src: url('./assets/fonts/Archivo-Medium.ttf');
  font-weight: 500;
}

@font-face {
  font-family: 'Archivo';
  src: url('./assets/fonts/Archivo-ExtraLight.ttf');
  font-weight: 200;
}

@font-face {
  font-family: 'Archivo';
  src: url('./assets/fonts/Archivo-Bold.ttf');
  font-weight: 600;
}

@font-face {
  font-family: 'Archivo';
  src: url('./assets/fonts/Archivo-ExtraBold.ttf');
  font-weight: 700;
}

/*GENERAL*/

body {
  margin: 0;
  font-family: 'Archivo';
  font-weight: 300;
}

#root {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

#highContrast {
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 1000;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: var(--button-color);
  color: var(--white);
  cursor: pointer;
}

/*MULTIUSED*/

.orange {
  color: var(--primary-color);
}

.green {
  color: var(--secondary-color);
  transition: .3s;
}

.lightFont {
  font-weight: 300;
}

.mediumFont {
  font-weight: 500;
}

.miniTitleText {
  font-size: 22px;
}

.paragraphText {
  font-size: 16px;
}

.smallText {
  font-size: 14px;
}

.miniText {
  font-size: 10px;
}

.exportText {
  font-size: 10px;
  margin: 0 auto;
  font-weight: 300;
}

/*GRID*/

.grid {
  position: relative;
  display: grid;
  grid-template-columns: 20px repeat(20, 1fr) 20px;
  grid-template-rows: repeat(20, 1fr);
  height: 100%;
  column-gap: 5px;
  z-index: 2;
}

/*LANDING*/

#landing {
  position: relative;
  height: 120vh;
  width: 100%;
  -webkit-mask-image: url('./assets/images/Landing_Mask.png');
  mask-image: url('./assets/images/Landing_Mask.png');
  mask-size: 100% 100%;
  mask-repeat: no-repeat;
  color: white;
}

#landing-background {
  position: absolute;
  left: 0;
  top: 0;
  background-image: url('./assets/images/background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
  width: 100%;
  filter: brightness(1);
  z-index: 1;
}

#landing .grid h1 {
  display: inline-grid;
  font-weight: 500;
  margin-top: 0;
  grid-column-start: 2;
  grid-column-end: 22;
  grid-row-start: 6;
  grid-row-end: 13;
  font-size: 80px;
  line-height: 85px;
  text-shadow: 0px 0px 3px #000;
}

#landingParagraphText {
  margin-top: 0;
  grid-column-start: 2;
  grid-column-end: 9;
  grid-row-start: 12;
  grid-row-end: 16;
}

#landingParagraphText p {
  background: white;
  display: inline-block;
  color: black;
  padding: 10px;
  border-radius: 20px;
}

#goDown {
  width: 100%;
  height: 100%;
  display: flex;
  border: 1px solid white;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: white;
  grid-column-start: 17;
  grid-column-end: 19;
  grid-row-start: 12;
  grid-row-end: 14;
  background-color: none;
  transition: .3s;
}

#goDown:hover {
  color: black;
  background-color: white;
}

#goDown span {
  font-size: 30px;
  margin-top: 0;
}

/*INTRODUCTION PAGE*/

#introduction {
  position: relative;
  grid-template-rows: none;
  height: auto;
  width: 100%;
  margin: 10vh 0 10vh 0;
}

#statistics {
  grid-column-start: 4;
  grid-column-end: 11;
}

#featuring {
  grid-column-start: 13;
  grid-column-end: 20;
}

/*VIZ SECTION*/

.visualisations {
  height: 100vh;
  transition: .5s;
}

.visualisationMap {
  height: 100vh;
}

.visualisationTable {
  height: 100vh;
}

#visualisations .grid h1 {
  align-self: end;
  grid-row-start: 1;
}

#chartButtons {
  grid-column-start: 1;
  grid-column-end: 23;
  display: grid;
  grid-template-columns: 20px repeat(20, 1fr) 20px;
  column-gap: 5px;
  padding-bottom: 10px;
}

.hoverableVizH1 {
  cursor: pointer;
  display: flex;
  align-self: flex-end;
}

.hoverableVizH1:hover {
  font-weight: 500;
  transform: translate(0, -3px) scaleY(1.1);
}

.vizButton {
  background-color: lightgray;
  border-radius: 10px;
  transition: .4s;
  opacity: .5;
  padding: 5px 10px;
  margin: 0;
}

.vizButtonActive {
  background-color: var(--primary-color);
  border-radius: 10px;
  padding: 5px 10px;
  transition: .5s;
  color: white;
  margin: 0;
}

#chartButton {
  grid-column-start: 2;
  grid-column-end: 6;
}

#mapButton {
  grid-column-start: 6;
  grid-column-end: 10;
}

#tableButton {
  grid-column-start: 10;
  grid-column-end: 14;
}

#viz {
  grid-row-start: 5;
  grid-column-start: 1;
  grid-column-end: 23;
  border-top: 1px solid black;
}

#loadingData {
  grid-row-start: 1;
  grid-row-end: 21;
  grid-column-start: 1;
  grid-column-end: 24;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.vizMap {
  grid-row-end: 19;
}

.vizChart {
  grid-row-end: 21;
}

.vizTable {
  grid-row-end: 21;
  overflow-y: scroll;
}

#exportTableDiv {
  width: 100vw;
  height: 80vh;
  overflow-x: auto;
  overflow-y: scroll;
  position: relative;
}

#exportTableDiv table {
  width: 100%;
}

#fixedTitleTable {
  position: sticky;
  top: 0;
  left: 0;
  background-color: white;
  z-index: 10;
  border-bottom: 1px solid #ccc;
  display: grid;
  grid-template-columns: 20px repeat(20, 1fr) 20px;
  column-gap: 5px;
  align-items: center;
}

#properties {
  grid-column-start: 1;
  grid-column-end: 23;
  grid-row-start: 2;
  grid-row-end: 5;
  z-index: 3;
  border-top: 1px solid black;
}

svg {
  display: block;
  width: 100%;
  shape-rendering: auto;
}

.chartSvg {
  overflow: visible;
}

.input {
  border: 1px solid var(--primary-color);
  border-radius: 10px;
  cursor: pointer;
  grid-row-start: 8;
  grid-row-end: 18;
  padding: 0 5px;
  font-size: 16px;
  z-index: 5;
}

.inputTitle {
  grid-row-start: 1;
  grid-row-end: 6;
}

.titles {
  grid-column-start: 5;
  grid-column-end: 22;
  text-align: left;
  font-size: 18px;
  margin: 15px 0 0 0;
  color: var(--primary-color)
}

.chartTitle {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  display: grid;
  grid-template-columns: 20px repeat(20, 1fr) 20px;
  column-gap: 5px;
  background: white;
  z-index: 10;
  border-bottom: 1px solid #ccc;
  align-items: center;
}

#subtitle {
  grid-column-start: 5;
  grid-column-end: 18;
  text-align: left;
  font-weight: 500;
  font-size: 12px;
  color: black;
  margin-top: 0;
}

.subtitle {
  grid-column-start: 5;
  grid-column-end: 18;
  text-align: left;
  font-weight: 500;
  font-size: 12px;
  color: black;
  margin-top: 0;
}

.chartTitle p {
  grid-column-start: 5;
  grid-column-end: 22;
  font-weight: bold;
  color: var(--primary-color);
  font-size: 16px;
  margin: .75% 0 0 0;
}

#noCategoriesTitle {
  grid-column-start: 2;
}

#dataCategoriesBar {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 22;
  font-size: 12px;
  border-radius: 5px;
  margin: 0;
}

#export {
  grid-column-start: 19;
  grid-column-end: 22;
  color: white;
  background-color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  font-weight: 500;
}

#export span {
  font-size: 20px;
}

/* Dropdown styling */
.dropdown {
  position: absolute;
  top: calc(100% - 10px);
  /* Position just below the #export div */
  right: 0;
  background-color: #FFFFFF;
  border: 1px solid var(--primary-color);
  border-radius: 5px;
  grid-column-start: 19;
  grid-column-end: 22;
  z-index: 10;
}

.dropdown-item {
  padding: 10px 20px;
  color: var(--primary-color);
  background-color: #FFFFFF;
  text-align: left;
  border: none;
  width: 100%;
  cursor: pointer;
}

.dropdown-item:hover {
  background-color: var(--primary-color);
  color: #FFFFFF;
}

/*MAP CHART*/

#mapContainer {
  grid-column-start: 1;
  grid-column-end: 24;
  grid-row-start: 4;
  grid-row-end: 21;
}

#mapContainer svg {
  height: 100%;
}

.indicatorMapInput {
  grid-column-start: 2;
  grid-column-end: 6;
}

.countryMapInput {
  grid-column-start: 6;
  grid-column-end: 10;
  grid-row-start: 8;
  grid-row-end: 18;
}

.indicatorMapTitle {
  grid-column-start: 2;
  grid-column-end: 6;
}

.countryMapTitle {
  grid-column-start: 6;
  grid-column-end: 10;
}

#timeline {
  grid-column-start: 1;
  grid-column-end: 23;
  grid-row-start: 19;
  grid-row-end: 21;
}

.timelineButtons {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-row-start: 6;
  grid-row-end: 16;
  border: 1px solid var(--primary-color);
  border-radius: 10px;
}

#year {
  grid-column-start: 2;
  grid-column-end: 4;
}

#mapYearText {
  grid-row-start: 8;
  grid-row-end: 18;
  grid-column-start: 11;
  grid-column-end: 13;
  text-align: center;
}

/* Container wrapping the slider and tick marks */
.slider-container {
  position: relative;
  grid-row-start: 8;
  grid-row-end: 10;
  grid-column-start: 5;
  grid-column-end: 21;
}

/* The slider itself keeps its styles */
#slider {
  width: 100%;
  height: 100%;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  background: var(--primary-color);
}

/* WebKit thumb styling */
#slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: white;
  border: 2px solid var(--primary-color);
  cursor: pointer;
  margin-top: 0px;
  /* Adjust this as needed to center the thumb vertically */
}

/* Firefox thumb styling */
#slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: white;
  border: 2px solid var(--primary-color);
  cursor: pointer;
}

/* Container for tick marks, positioned absolutely relative to .slider-container */
.ticks {
  position: absolute;
  top: 300%;
  /* Place ticks just below the slider */
  left: 0;
  width: 100%;
  height: 20px;
  /* Adjust if needed */
}

/* Tick mark styling */
.tick {
  font-size: 0.5rem;
  color: #000;
  /* Change the color as needed */
  white-space: nowrap;
}

.tooltip {
  position: absolute;
  border: 1px solid white;
  border-radius: 10px;
  padding: 10px;
  font-size: 12px;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  background-color: var(--primary-color);
  z-index: 5;
  color: white;
}

.tooltip h1 {
  font-size: 16px;
  margin: 0;
}

#legend {
  position: absolute;
  top: 15%;
  right: 1%;
  width: 50px;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  padding: 20px;
}

#legend2 {
  position: absolute;
  top: 15%;
  right: calc(1% + 75px);
  width: 50px;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  padding: 20px;
}

.legendItem {
  width: 100%;
  height: 20%;
  display: flex;
  font-size: 10px;
  text-align: right;
  justify-content: right;
}

.legendColor {
  height: 90%;
  width: 20px;
  margin-left: 5px;
  border-radius: 5px;
}

.mapInfo {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 20%;
  height: 90%;
  background-color: white;
  border-radius: 0 10px 10px 0;
  border-right: var(--primary-color) 1px solid;
  transform: translateX(-100%);
  transition: 1s;
  padding: 0 10px;
}

.mapInfo h2 {
  font-size: 18px;
  font-weight: 500;
  color: var(--primary-color);
}

.mapInfo h3 {
  font-size: 15px;
  font-weight: 400;
  color: black;
}

.mapInfo p {
  font-size: 13px;
  font-weight: 200;
}

#fixedTitleMap {
  position: sticky;
  grid-column-start: 1;
  grid-column-end: 23;
  grid-row-start: 1;
  grid-row-end: 3;
  height: auto;
  background-color: white;
  z-index: 10;
  padding: 10px 20px;
  border-bottom: 1px solid #ccc;
}

/*TABLE CHART*/

.typeTableInput {
  grid-column-start: 2;
  grid-column-end: 6;
}

.indicatorTableInput {
  grid-column-start: 6;
  grid-column-end: 10;
}

.countryTableInput {
  grid-column-start: 10;
  grid-column-end: 14;
}

.yearTableInput {
  grid-column-start: 14;
  grid-column-end: 18;
}

#viz table {
  grid-column-start: 1;
  grid-column-end: 23;
  grid-row-start: 2;
  border-collapse: collapse;
  margin-bottom: 20px;
}

#viz table tbody {
  text-align: center;
}

td,
th {
  padding: 8px 20px;
}

thead.orange th {
  position: sticky;
  background-color: #ffffff;
  top: 62px;
  z-index: 5;
  padding: 8px 20px;
}

.normal-row {
  border: 1px solid #dddddd;
}

.normal-row:nth-child(even) {
  background-color: #dddddd;
}

.normal-row:hover {
  background-color: var(--primary-color);
  color: white;
}

.last-entry-row {
  background-color: white;
  border: 1px solid var(--primary-color);
  border-radius: 10px;
  padding: 12px;
  text-align: left;
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
}

.last-entry-field {
  background-color: white;
  border: 1px solid var(--primary-color);
  border-radius: 10px;
  padding: 12px;
  text-align: left;
  box-sizing: border-box;
  margin-left: 50px;
  margin-right: 10px;
}

.last-entry-country {
  background-color: white;
  border: 1px solid var(--secondary-color);
  border-radius: 10px;
  padding: 12px;
  text-align: left;
  box-sizing: border-box;
  margin-left: 90px;
  margin-right: 10px;
  font-weight: 500;
}

/*CHART CHART*/

.typeChartInput {
  grid-column-start: 2;
  grid-column-end: 6;
}

.indicatorChartInput {
  grid-column-start: 6;
  grid-column-end: 10;
}

.countryChartTitle {
  grid-column-start: 10;
  grid-column-end: 14;
}

.countryChartInput {
  grid-row-start: 8;
  grid-row-end: 18;
  font-size: 16px;
  grid-column-start: 10;
  grid-column-end: 14;
}

.yearChartInput {
  grid-column-start: 14;
  grid-column-end: 18;
}

#chartContainer {
  position: relative;
  grid-column-start: 2;
  grid-column-end: 22;
  grid-row-start: 1;
  grid-row-end: 22;
  height: 100%;
}

.barContainer {
  overflow-y: auto;
}

#chartScroll {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 30px;
  overflow-y: auto;
}

#chartScroll::-webkit-scrollbar {
  width: 15px;
}

#chartScroll::-webkit-scrollbar-track {
  background: white;
  border: 1px solid var(--primary-color);
  border-radius: 8px;
}

#chartScroll::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
  border: 2px solid white;
  /* Creates space around thumb */
  border-radius: 8px;
}

.chartLegend {
  position: absolute;
  left: 0;
  top: calc(80px + 5%);
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  z-index: 3;
  height: 75%;
  font-size: 12px;
}

.legend-item {
  padding: 2px 0;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  width: 100%;
  cursor: pointer;
}

.legend-item div {
  margin-right: 2px;
  width: 10px;
  height: 100%;
}

.legend-item p {
  margin: 0;
}

#legendPile {
  position: absolute;
  bottom: 30px;
  left: 2%;
  width: 100%;
  height: 3%;
  font-size: 12px;
  display: flex;
  justify-content: left;
}

.hiddenLegend #legendPile {
  position: absolute;
  bottom: -20px;
  left: 2%;
  width: 100%;
  height: 3%;
  font-size: 12px;
  display: flex;
  justify-content: left;
}

#dataCategoriesPie {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 19;
  grid-row-end: 20;
  font-size: 12px;
  border-radius: 5px;
}

.scrollHint {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 12px;
  padding: 4px 8px;
  border-radius: 5px;
  pointer-events: none;
  z-index: 20;
}

#credits {
  position: absolute;
  font-size: 10px;
  width: auto;
  left: 2%;
  bottom: 0px;
  color: black;
  z-index: 9999;
}

/*FOOTER*/

.sharingLines,
.dataLines {
  border-top: 1px solid black;
  grid-column-start: 1;
  grid-column-end: 23;
}

.dataLines {
  grid-row-start: 2;
  grid-row-end: 3;
}

.sharingLines {
  grid-row-start: 10;
  grid-row-end: 11;
}

#initialTerms {
  height: auto;
  grid-template-rows: none;
}

#midTerms {
  height: auto;
  grid-template-rows: none;
}

#midTerms h2 {
  cursor: pointer;
}

#technical {
  grid-column-start: 3;
  grid-column-end: 12;
  height: auto;
}

#regional {
  grid-column-start: 13;
  grid-column-end: 22;
  height: auto;
}

#regional a {
  text-decoration: none;
  cursor: pointer;
}

.collapsible-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
}

.arrow {
  display: inline-block;
  transition: transform 0.3s ease;
  margin-left: 10px;
  font-size: 8px;
  transform: rotate(-90deg);
  transition: .25s;
  /* Adjust spacing as needed */
}

/* Use a pseudo-element to display a down arrow */
.arrow::before {
  content: '▼';
}

/* Rotate the arrow when the collapsible is open */
.arrow.open {
  transform: rotate(-180deg);
}

/* Hover effect: overrides orange/green with black */
.collapsible-header:hover .arrow {
  color: black;
}

.toggle {
  margin-left: 10%;
  overflow: hidden;
  transition: height ease 0.5s;
}

.toggle p {
  margin: 0 0 5px 0;
}

#lastTerms {
  margin-top: 2vh;
  height: auto;
  background-color: var(--primary-color);
  color: white;
  grid-template-rows: none;
}

#dataTerms {
  grid-column-start: 2;
  grid-column-end: 22;
  grid-row-start: 1;
  grid-row-end: 2;
}

#sharingTerms {
  grid-column-start: 2;
  grid-column-end: 22;
}

#sharingTerms a {
  color: white;
}

@media screen and (max-width: 850px) {
  #landing .grid h1 {
    font-size: 64px;
    line-height: 60px;
    text-shadow: 0px 0px 3px #000;
  }

  #landingParagraphText {
    grid-column-start: 2;
    grid-column-end: 9;
    grid-row-start: 12;
    grid-row-end: 14;
    font-size: 20px;
  }

  #goDown {
    grid-column-start: 15;
    grid-column-end: 19;
    grid-row-start: 12;
    grid-row-end: 14;
  }

  #introduction {
    display: flex;
    flex-direction: column;
    width: 90%;
    position: relative;
    left: 5%;
  }

  .chartTitle {
    height: 80px;
  }

  .subtitle {
    grid-column-end: 22;
    font-size: 10px;
  }

